<template>
  <div class="v-form-item">
    <label
      v-if="!isNoLabel"
      :style="{ width: labelWidth + 'px' }"
      ref="label"
      :class="[
        sizeCss,
        {
          'v-form-item__label--right': labelPosition === 'right'
        }
      ]"
      class="v-form-item__label"
    >
      <span ref="labelTxt" v-if="label">{{ label }}</span>
      <slot name="label"></slot>
    </label>
    <div
      class="v-form-item__content"
      :style="{ 'margin-left': !isNoLabel ? labelWidth + 20 + 'px' : '0' }"
    >
      <slot></slot>
      <!-- <div
        class="v-form-item__content__msg is-error"
        v-if="errorMsg && !isFoucs"
      >
        {{ errorMsg }}
      </div> -->
    </div>
  </div>
</template>
<script>
import { checkData } from "../libs";
export default {
  name: "v-form-item",
  props: {
    //
    label: String,
    //
    isNoLabel: {
      type: Boolean,
      default: false
    },
    //
    prop: String,
    //
    required: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    //
    labelWidth() {
      return this.$getLabelWidth();
    },
    sizeCss() {
      let cssObj = {
        M: "v-form-item__label--medium",
        S: "v-form-item__label--small",
        L: "v-form-item__label--large"
      };
      return cssObj[this.size] || cssObj.M;
    },
    //
    disabled() {
      return this.childNode.disabled;
    },
    //
    value() {
      return this.childNode.value;
    },
    //
    size() {
      return this.childNode.size;
    }
  },
  data() {
    return {
      isMounted: false, //
      errorMsg: "", //
      valid: [], //form
      isFoucs: false, //
      labelPosition: "left", //
      childNode: {} //Vnode
    };
  },

  created() {
    //
    this.$on("form:created", vnode => {
      if (!this.isMounted) {
        this.childNode = vnode;
        this.isMounted = true;
      }
    });
    //
    this.$on("form:blur", () => {
      this.checkValid();
    });
    //
    this.$on("form:check", () => {
      this.checkValid();
    });
    //form
    this.$dispatch("v-form", "form.addField", this);
  },

  mounted() {
    //
    this.changeTitle();
  },
  methods: {
    //
    checkValid(val) {
      let result = true;
      val = val || this.value;
      result = checkData.call(
        this,
        {
          disabled: this.disabled,
          valid: this.valid,
          required: this.required,
          ignore: !this.prop
        },
        val
      );
      //
      this.setError(result);
      return !result;
    },
    setError(msg) {
      this.errorMsg = msg;
      this.childNode.error = msg;
    },
    changeTitle() {
      if (this.$refs.labelTxt) {
        let width = this.$refs.labelTxt.offsetWidth;
        //
        this.$dispatch("v-form", "label-width", width);
      }
    }
  },
  watch: {
    title: {
      handler() {
        this.changeTitle();
      }
      //immediate: true
    }
  },
  //
  destroyed() {
    this.$dispatch("v-form", "form.removeField", this);
    this.childNode = {};
  }
};
</script>
