<template>
  <div class="v-table__footer">
    <v-pagination
      :total="total"
      :current-page="page"
      is-show-total
      :page-size="pageSize"
      :is-change-size="isChangeSize"
      :is-input-page="isInputPage"
      :page-size-options="pageSizeOptions"
      :border="false"
      @change-page="changePage"
      @change-size="changeSize"
    ></v-pagination>
  </div>
</template>
<script>
export default {
  props: {
    total: Number, //
    page: Number, //
    pageSize: Number,
    isChangeSize: Boolean, //
    isInputPage: Boolean, //
    pageSizeOptions: Array //
  },
  methods: {
    changePage(page) {
      this.$emit("change-page", page);
    },
    changeSize(pageSize) {
      this.$emit("change-size", pageSize);
    }
  }
};
</script>
